//     color: white;
// }

.leftSide-contacts {
    display: flex;
    flex-direction: column;
    @include desktop {
        margin-left: 40px;
    }
    @include MINtablet {
        align-items: flex-start;
        margin-left: 15px;
    }
}

// form
.form-title {
    margin-top: 98px;
    margin-bottom: 20px;
    text-align: center;
    color: white;
    font-size: 1.4rem;
    // width: 100%;
    @include MINtablet {
        text-align: start;
        max-width: 500px;
    }
    @include tablet {
        margin-top: 115px;
        font-size: 2rem;
    }
    @include desktop {
        max-width: inherit;
    }
}

.form-wrap {
    box-shadow:
        rgba(47, 47, 47, 0.137) 0px 14px 28px,
        rgba(0, 0, 0, 0.17) 0px 10px 10px;
    background-color: rgba(202, 202, 202, 0.147);
    padding: 30px 20px;
    padding-top: 20px;
    backdrop-filter: blur(14px);

    @include MINtablet {
        padding: 30px 20px;
        // margin: 0 auto;
    }
    @media screen and (min-width: 1280px) {
        width: 600px;
    }
}

.form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;

    @include MINtablet {
        row-gap: 20px;
    }
}

.first-line-form,
.second-line-form,
.third-line-form {
    @include MINtablet {
        display: flex;
        align-items: end;
        column-gap: 5px;
        margin-bottom: 5px;
    }
}

.label {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 5px;
    @include MINtablet {
        row-gap: 5px;
        width: 210px;
    }
    @include desktop {
        width: 277px;
    }
}

input {
    outline: none;
    border: none;
    padding: 5px 10px;
    height: 38.4px;
    width: 100%;
    margin-top: 5px;
    @include MINtablet {
        margin-top: 0;
    }
}

.inputAddress {
    margin-top: 5px;

    @include MINtablet {
    }
}

.submit {
    // background-color: rgb(169, 169, 23);
    background-color: #0d7331;
    color: white;
    text-transform: uppercase;
    border: 0;
    padding: 10px 20px;
    font-size: 1.5rem;
    font-weight: 500;
    transition: background-color 0.3s ease-in-out;
    margin-top: 20px;
    @include MINtablet {
        margin-top: 0;
    }
}

// footer section
.footer {
    background-color: rgb(72, 91, 74);
    // height: 20vh;
    color: white;
}

.footer-contacts {
    display: flex;
    flex-direction: column;
    padding: 10px 20px;
    @include tablet {
        align-items: center;

        padding: 20px 40px;

        flex-direction: row;
    }
}
.footer-list {
    display: flex;
    justify-content: space-between;

    // flex-direction: column;
    // justify-content: space-between;
    // align-items: center;
    // padding: 20px 40px;
    @include tablet {
        column-gap: 60px;
        margin-left: 6vw;
    }
    @include desktop {
        margin-left: 130px;
    }
}

.footer-list-item {
    display: flex;
    flex-direction: column;
    row-gap: 5px;
    font-style: normal;
    font-weight: 400;
    font-size: 0.8rem;
    @include tablet {
        row-gap: 10px;
    }
}

.link {
    color: rgba(189, 189, 189, 0.697);
    text-decoration: none;
    font-weight: 500;
    font-size: 0.6rem;
    display: flex;
    align-items: center;
    transition: color 0.3s ease-in-out;
    &:hover {
        color: white;
    }

    &:hover .footer-icon {
        fill: white;
    }
}
.footer-icon {
    fill: rgba(189, 189, 189, 0.697);
    margin-right: 5px;
    width: 10px;
    height: 10px;
    transition: fill 0.3s ease-in-out;
}

// social
.footer-social {
    display: flex;
    column-gap: 20px;
    flex-direction: column;
    margin: 0 auto;
    padding-top: 5px;
    @include MINtablet {
        padding-top: 0;
    }
}

.footer-link-address {
    // width: 200px;
    // line-height: 1.6;
}

.footer-social-list {
    display: flex;
    column-gap: 20px;
}

.footer-social-icon {
    fill: white;
    background-color: #dbdbdb39;
    padding: 10px;
    &:hover {
        //    fill: #0d7331;
        background-color: #dbdbdb14;
    }
}

.phones {
    display: flex;
    flex-direction: column;
    row-gap: 8px;
}

.submit:hover {
    background-color: #219048;
}
